.homepage {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.homepage__filter__search {
  background: #fff;
  display: flex;
  border: 1px solid #dfe1e5;
  box-shadow: none;
  height: 39px;
  width: 690px;
  border-radius: 24px;
  z-index: 3;
  height: 44px;
  margin: 20px auto;
  box-shadow: 0 1px 6px 0 rgb(32 33 36 / 28%);
}

.homepage__filter__search__input {
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  color: rgba(0, 0, 0, 0.87);
  word-wrap: break-word;
  outline: none;
  display: flex;
  flex: 100%;
  -webkit-tap-highlight-color: transparent;
  height: 34px;
  font-size: 16px;
  flex: 1;
  display: flex;
  padding: 5px 4px 0 16px;
  padding-left: 14px;
}
.homepage__show{
    padding: 20px;
    background: #fff;
    border: 1px solid #dfe1e5;
    box-shadow: 0 1px 6px 0 rgb(32 33 36 / 28%);
}

.homepage__display__headings{
    display:grid;
    grid-template-columns: repeat(4,1fr);
    border-top: 1px solid black;
    margin:20px auto;
    
}


.homepage__display__headings > span{
padding: 8px 4px;
/* border-left: 1px solid black;
border-right: 1px solid black; */
border-bottom: 1px solid black;
}

@media (max-width: 540px) {
  .homepage__filter__search {
    width: 310px;
  }
}
